.App{height:100%;min-height:400px;text-align:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:stretch;justify-content:stretch}.App-flex{-webkit-flex:1;flex:1;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:column;flex-direction:column}.App-heading{background-color:#222;color:#f8f8f8;font-size:6vh;box-shadow:0px 4px 4vh 4px rgba(34,34,34,0.9);z-index:2}.App-react{color:#00d8ff;text-decoration:overline underline}.App-logo{max-height:30vh;max-width:30vh}.App-instructions{background-color:#f8f8f8;color:#222;font-size:3vh;line-height:1.5;padding:0 1em}.App-instructions code{background-color:#222;color:#00d8ff;padding:.2em .3em;border-radius:.2em}
